/* #NORMALIZE
-------------------------------------------------- */
html, body {
  height: 100%;
  margin: 0; }

article, aside, details, footer, header, main, nav, section {
  display: block; }

audio, canvas, progress, video {
  display: inline-block;
  vertical-align: baseline; }

a, button, input {
  -webkit-focus-ring-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-colo: rgba(0, 0, 0, 0); }

span, h1, h2, h3, h4, h5, h6, p, a, img, small, sub, sup, b, i, ul, li {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline; }

sub, sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

ul {
  list-style: none; }

b, strong {
  font-weight: 700; }

small {
  font-size: 80%; }

i {
  font-style: italic; }

blockquote {
  margin: 0; }

svg:not(:root) {
  overflow: hidden; }

textarea {
  overflow: auto;
  box-sizing: border-box;
  resize: vertical; }

input {
  box-sizing: border-box;
  line-height: normal; }

button, input[type='button'], input[type='reset'], input[type='submit'] {
  cursor: pointer;
  -webkit-appearance: button; }

button[disabled], input[disabled] {
  cursor: default; }

button::-moz-focus-inner, input::-moz-focus-inner {
  padding: 0;
  border: 0; }

input[type='checkbox'], input[type='radio'] {
  padding: 0; }

input[type='number']::-webkit-inner-spin-button, input[type='number']::-webkit-outer-spin-button {
  height: auto; }

input[type='search'] {
  box-sizing: content-box;
  -webkit-appearance: textfield; }
  input[type='search']::-webkit-search-cancel-button, input[type='search']::-webkit-search-decoration {
    -webkit-appearance: none; }

input[type='submit'] {
  border-radius: 0;
  outline: none;
  box-shadow: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

input[type='text']::-ms-clear {
  display: none; }

input:invalid {
  outline: none;
  box-shadow: none; }

input:-moz-ui-invalid {
  outline: none;
  box-shadow: none; }

table {
  border-spacing: 0;
  border-collapse: collapse; }

tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline; }

:root {
  --theme-main-font: 'Lato', sans-serif;
  --theme-font-size: 16px;
  --theme-text-color: #222222;
  --theme-text-color-light: #6f6f6f; }

* {
  color: #222222;
  color: var(--theme-text-color);
  font-family: 'Lato', sans-serif;
  font-family: var(--theme-main-font);
  font-size: 16px;
  font-size: var(--theme-font-size); }

#root, .rt-app {
  height: 100%; }

