.rt-app-loading {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }
  .rt-app-loading__body {
    text-align: center; }
  .rt-app-loading__title {
    margin-top: 15px;
    font-weight: 700;
    font-size: 24px; }
  .rt-app-loading__subtitle {
    color: #6f6f6f;
